.cs_lotto .win_area.type_lotto .ball.type1,
.ball.type1 {
  background-color: #f2b720;
}

.cs_lotto .win_area.type_lotto .ball.type2,
.ball.type2 {
  background-color: #4072ac;
}

.cs_lotto .win_area.type_lotto .ball.type3,
.ball.type3 {
  background-color: #de4c0e;
}

.cs_lotto .win_area.type_lotto .ball.type4,
.ball.type4 {
  background-color: #9195a4;
}

.cs_lotto .win_area.type_lotto .ball.type5,
.ball.type5 {
  background-color: #13be4b;
}

.cs_lotto .win_area.type_lotto .ball,
.ball {
  border-radius: 100%;
  width: 30px;
  height: 30px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  color: #fff;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  line-height: 30px;
}

.numList {
  display: flex;
  justify-content: space-around;
}
